@font-face {
    font-family: SourceSansPro-Light; 
    src: url('./resources/Source_Sans_Pro/SourceSansPro-Light.ttf'); }
@font-face {
    font-family: SourceSansPro-Regular;
    src: url('./resources/Source_Sans_Pro/SourceSansPro-Regular.ttf');
}
@font-face {
    font-family: SourceSansPro-Bold;
    src: url('./resources/Source_Sans_Pro/SourceSansPro-Bold.ttf');
}
@font-face {
    font-family: SourceSansPro-SemiBold;
    src: url('./resources/Source_Sans_Pro/SourceSansPro-SemiBold.ttf');
}

*,
*::after,
*::before {
	margin: 0;
	padding: 0;
    box-sizing: inherit;
    font-family: SourceSansPro-Light, Arial, sans-serif;
    background-color: #F0F0F7;
}

